<template>
  <div class="clearfix set">
    <section id="header">
     <HeaderPublc></HeaderPublc>
    </section>
    <section id="cent">
        <cent></cent>
    </section>
    <section id="footer"> </section>
  </div>
</template>
<style scoped>
  .set{width: 100%;background:#fff;overflow: hidden; *zoom: 1;}
  body{background-color:#f7f7f7}
  #cent{width: 100%;margin-left: 230px}
  #header{float: left;width: 230px}

</style>
<script>
  import HeaderPublc from '../public/set.vue'
  import cent from '../public/divesList.vue'
  export default {
    name: 'my-login',
    data () {
      return {
        fromData: {
          userName: null, // 账号
          password: null, // 密码
          clientType: 1 // 客户端类型
        },
        remember: false, // 是否记住密码
        error: false, // 登陆是否成功
        defaultLayers: null,
        ui: null,
        list: [],
        bubble: null,
        routeInstructionsContainer: null,
        input: null
      }
    },
    methods: {
      login () {
        this.$router.push({ name: "map" })
      }
    },
    computed: {

    },
    mounted () {
      // 配置顶部菜单
      var parm = {logo: false, login: false, us: true, language: true, user: false, nav: false}
      this.$store.commit('updateNave', parm)
    },
    components: {
      HeaderPublc: HeaderPublc,
      cent: cent
    }
  }
</script>




